:root {
  --darkRed: #8c0d17;
  --darkGrey: #496373;
  --lightGrey: #ced5da;
  --white: #fff;
}

@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 100 1000;
  font-stretch: 25% 151%;
  src: url("../../RobotoFlex-VariableFont_GRAD,XTRA,YOPQ,YTAS,YTDE,YTFI,YTLC,YTUC,opsz,slnt,wdth,wght.b198d94c.ttf");
}

*, :before, :after {
  box-sizing: 0;
  border: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.navUser {
  background-color: var(--lightGrey);
  width: 100%;
  height: auto;
  border-bottom: solid 1px var(--darkGrey);
  align-items: center;
  display: flex;
}

.navAvaUser {
  width: 50px;
  height: 50px;
  background-color: var(--darkGrey);
  border-radius: 50%;
  justify-content: space-around;
  align-items: center;
  margin: 10px;
  display: flex;
}

.avatarUser {
  color: var(--white);
  text-align: center;
}

.searchChat {
  border-bottom: 1px solid var(--lightGrey);
  border-radius: 15px;
  outline: none;
  margin-left: 15px;
  margin-right: 15px;
  padding: 7px;
}

.searchChat::placeholder {
  text-align: center;
  color: var(--lightGrey);
}

.searchChat:focus, .searchChat:active {
  border: 1px solid var(--darkRed);
}

.chatUser {
  background-color: var(--white);
  width: 100%;
  height: auto;
  border-bottom: solid 1px var(--lightGrey);
  grid-template-columns: 20% auto 15%;
  place-items: center start;
  display: grid;
}

.chatUser:hover {
  opacity: .9;
}

.chatAvaUser {
  width: 40px;
  height: 40px;
  background-color: var(--lightGrey);
  border-radius: 50%;
  justify-content: space-around;
  align-items: center;
  margin: 10px;
  display: flex;
}

.avatarUser {
  color: var(--white);
  text-align: center;
  font-size: 13px;
}

.nameUser {
  color: var(--darkGrey);
  flex-direction: column;
  padding-left: 10px;
  display: flex;
}

.nameUser > h3 {
  padding-bottom: 3px;
  font-size: 16px;
  font-weight: 500;
}

.nameUser > p {
  font-size: 13px;
  font-weight: 200;
}

.storyUser {
  color: var(--lightGrey);
  flex-direction: column;
  align-items: center;
  font-size: 12px;
  font-weight: 200;
  display: flex;
}

.storyUser > time {
  padding-bottom: 10px;
}

.alertUser {
  width: 20px;
  height: 20px;
  background-color: var(--darkRed);
  border-radius: 50%;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.alertUser > p {
  color: var(--white);
  text-align: center;
  font-size: 12px;
}

.formSendMessage {
  border-top: 1px solid var(--lightGrey);
  flex-direction: row;
  flex: none;
  margin: 15px 30px;
  padding-top: 15px;
  display: flex;
}

.imgSendMessage {
  width: 30px;
  height: auto;
  color: var(--darkRed);
  padding: 0 15px;
}

.inputMessage {
  width: 100%;
  border: 1px solid var(--lightGrey);
  border-radius: 15px 0 0 15px;
  outline: none;
  padding-left: 15px;
}

.inputMessage:focus, .inputMessage:active {
  border: 1px solid var(--darkRed);
}

html, body {
  height: 100%;
  color: var(--darkGrey);
  font-family: Roboto;
}

main {
  display: flex;
}

.asideChats {
  min-height: 100vh;
  background-color: var(--darkGrey);
  width: 30%;
  flex-direction: column;
  display: flex;
}

.asideChat {
  min-height: 100%;
  background-color: var(--white);
  width: 100%;
  flex-direction: column;
  display: flex;
}

.messageUser {
  flex: 1 0 auto;
  justify-content: center;
  align-items: center;
  display: flex;
}

.messageUser > h3 {
  font-weight: 400;
}

